.Blackground {
  padding: 0;
  padding-top: 0px;
  background-color: black;
  height: 50vh;
}

.Container {
  padding-top: 2%;
  padding-left: 15%;
  padding-right: 15%;
}

.SynthTitle {
  color: #fff;
  margin-bottom: 0;
  width: 100%;
  display: block;
  margin-top: 0px;
  line-height: 110%;
  text-shadow: 5px 0px 2px rgba(255, 0, 255, 0.5),
    -4px 0px 5px rgba(0, 255, 191, 0.5), 5px 0px 10px rgba(49, 122, 255, 1);
}

.BigText {
  font-size: 35px;
}

.MediumText {
  font-size: 25px;
}

.SmallText {
  font-size: 11px;
}

.AboutMeSynthBox {
  margin-top: 20px;
  min-height: 25vh;
  background-color: #1a0d1e;
  border-color: #964eb3;
  vertical-align: middle;
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 10px;
  border: 1px solid #964eb3;
  text-align: left;
}
.ContactMeSynthBox {
  margin-top: 20px;
  min-height: 25vh;
  background-color: #1a0d1e;
  border-color: #964eb3;
  vertical-align: middle;
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 10px;
  border: 1px solid #964eb3;
  text-align: left;
  margin-left: 25px;
}

.ContactMeSynthBox a {
  color: white;
  text-decoration: none;
}
.ContactMeSynthBox a :hover {
  transition: 0.7s;
  color: #fff;
  text-shadow: 5px 0px 2px rgba(23, 124, 219, 0.5),
    -4px 0px 5px rgba(0, 255, 191, 0.5), 5px 0px 10px rgba(49, 122, 255, 1);
}

.SynthText {
  color: #fff;
  margin-bottom: 0;
  width: 100%;
  display: block;
  margin-top: 0px;
  font-size: 15px;
  line-height: 110%;
  text-shadow: 5px 0px 2px rgba(255, 0, 255, 0.5),
    -4px 0px 5px rgba(0, 255, 191, 0.5), 5px 0px 10px rgba(49, 122, 255, 1);
}


.glow {
  border-radius: 10px;
  color: #fff;
  box-shadow: 0 0 2px #fff, 0 0 10px #fff, 0 0 20px #964eb3, 0 0 30px #964eb3,
    0 0 40px #964eb3, 0 0 50px #964eb3;
  -webkit-animation: blink 1.0s infinite alternate;
  animation: blink 1.0s infinite alternate;
}

@-webkit-keyframes blink {
  100% {
    box-shadow: 0 0 3px #fff,
    0 0 10px #fff,
    0 0 20px #fff,
    0 0 40px #964eb3,
    0 0 70px #964eb3, 0 0 80px #964eb3;
  }
}

@keyframes blink {
  100% {
    box-shadow: 0 0 3px #fff, 0 0 10px #fff, 0 0 20px #fff, 0 0 40px #964eb3,
      0 0 70px #964eb3, 0 0 50px #964eb3;
  }
}

@media screen and (max-width: 900px) {
  .Blackground {
    height: 75vh;
  }
  .ContactMeSynthBox {
    margin-top: 30px;
    margin-left: 0;
  }
}
